html {
  font-size: 62.5%;
  box-sizing: border-box;
  scroll-padding-top: 0rem;
  background-color: #fdf1f1;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  font-family: "Yanone Kaffeesatz", sans-serif;
  font-size: 1.6rem;
}
body.fijar-body {
  overflow: hidden;
}
body.body-scroll {
  padding-top: 8.6rem;
}

p {
  font-size: 2rem;
  color: #000000;
  line-height: 1.5;
  text-transform: lowercase;
}

.contenedor {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

h1, h2, h3 {
  margin: 0 0 5rem 0;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.6rem;
}

h3 {
  font-size: 3rem;
}

img {
  max-width: 100%;
  width: 100%;
  height: auto;
  display: block;
}

.portada {
  font-family: "Silkscreen", cursive;
  color: #FFFFFF;
}
.portada span {
  color: #cfe3fa;
  text-transform: uppercase;
}
@media (min-width: 768px) {
  .portada span {
    color: #94c6ff;
  }
}

.avatar {
  width: 20rem;
  height: auto;
  align-items: center;
}
@media (min-width: 768px) {
  .avatar {
    width: 25rem;
    height: auto;
    margin-left: 5rem;
  }
}
@media (min-width: 992px) {
  .avatar {
    width: 30rem;
    height: auto;
    margin-left: 15rem;
  }
}

.imagenP {
  position: relative;
  max-height: 70rem;
  min-height: 50rem;
  overflow: hidden;
}
.imagenP .overlay {
  position: absolute;
  background: rgb(176, 73, 152);
  background: radial-gradient(circle, rgba(176, 73, 152, 0.5264957265) 3%, rgba(201, 124, 158, 0.1695156695) 97%);
  backdrop-filter: blur(0.28rem);
  height: 100%;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
}
@media (min-width: 768px) {
  .imagenP .overlay {
    background: rgb(176, 73, 152);
    background: radial-gradient(circle, rgba(176, 73, 152, 0.2564957265) 0%, rgba(201, 124, 158, 0.1695156695) 97%);
    backdrop-filter: blur(0.1rem);
  }
}
.imagenP picture {
  max-width: auto;
  min-width: 70rem;
  display: block;
}
@media (min-width: 992px) {
  .imagenP picture {
    width: 100%;
  }
}

.contenido-img {
  display: flex;
  flex-direction: column;
  justify-content: center;
  text-align: center;
  align-items: center;
}
@media (min-width: 768px) {
  .contenido-img {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 1rem;
    align-content: end;
  }
  .contenido-img h2 {
    align-items: center;
    margin-bottom: 0;
    padding-top: 10rem;
    padding-right: 5rem;
  }
}

.header {
  background-color: #d0697c;
}
@media (min-width: 768px) {
  .header.fijo {
    left: 0;
    top: 0;
    position: fixed;
    width: 100%;
    box-shadow: 0.1rem 0.2rem 0.3rem #333;
  }
}

.navegacion {
  display: flex;
  flex-direction: column;
  padding-top: 0.8rem;
  padding-bottom: 0.8rem;
}
.navegacion img {
  width: 8rem;
  display: flex;
  justify-content: center;
}
@media (min-width: 768px) {
  .navegacion {
    flex-direction: row;
    justify-content: space-between;
  }
}

@media (min-width: 768px) {
  .navegacion-principal {
    display: flex;
    align-items: center;
  }
}
.navegacion-principal a {
  color: #FFFFFF;
  text-decoration: none;
  font-size: 2rem;
  display: block;
  text-align: center;
}
@media (min-width: 768px) {
  .navegacion-principal a {
    margin-right: 2rem;
  }
  .navegacion-principal a:last-of-type {
    margin-right: 0;
  }
}
.navegacion-principal a:hover {
  color: #fac2c2;
}

.sobre-mi {
  margin-top: 5rem;
}
.sobre-mi p {
  text-transform: lowercase;
}

.logos {
  margin-top: 3rem;
  margin-left: 6rem;
  text-align: center;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 1rem;
}
.logos img:hover {
  filter: brightness(70%);
}
.logos .uno {
  width: 5.1rem;
}
.logos .dos {
  width: 5.3rem;
}
.logos .tres {
  width: 8.6rem;
}
.logos .cuatro {
  width: 3.5rem;
}
.logos .cinco {
  width: 5.1rem;
}
.logos .seis {
  width: 10rem;
}
@media (min-width: 768px) {
  .logos {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 1rem;
  }
}

.pdf {
  background-color: #d0697c;
  font-size: 2rem;
  font-weight: bold;
  padding: 1rem 4rem 1rem 0;
  margin: 0 6rem 2rem 6rem;
  text-align: center;
  border-radius: 2rem;
}
@media (min-width: 768px) {
  .pdf {
    margin: 0 56rem 2rem 0rem;
  }
}
@media (min-width: 992px) {
  .pdf {
    margin: 0 100rem 2rem 0rem;
  }
}
.pdf a {
  color: #FFFFFF;
  position: relative;
}
.pdf a:hover {
  color: #fac2c2;
}
.pdf svg {
  position: absolute;
  top: -0.8rem;
}

.portafolio {
  margin-bottom: 20rem;
}

@media (min-width: 768px) {
  .contacto {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 1rem;
  }
}

@media (min-width: 768px) {
  .correo {
    margin-top: 10rem;
  }
}
.correo img {
  width: 5rem;
}
.correo h3 {
  margin-bottom: 0;
  margin-left: 0.9rem;
}
.correo p {
  margin: 0.5rem 0 3rem 5rem;
}
.correo .correo-contenedor {
  display: grid;
  grid-template-columns: 10% 90%;
}

.redSocial {
  margin-bottom: 5rem;
}
.redSocial img {
  width: 4rem;
  margin-left: 0.5rem;
}
.redSocial h3 {
  margin-bottom: 0.8rem;
  margin-left: 1rem;
}
.redSocial a {
  color: #000000;
  font-size: 2rem;
  margin-left: 5rem;
}
.redSocial .redSocial-contenedor {
  display: grid;
  grid-template-columns: 10% 90%;
  align-items: center;
}

.formulario {
  display: block;
  width: 30rem;
  height: 40rem;
  margin-left: 2rem;
  background-color: #FFFFFF;
  margin-bottom: 5rem;
  border-radius: 2rem;
  text-align: center;
}
@media (min-width: 768px) {
  .formulario {
    width: 40rem;
    height: 40rem;
    margin-left: 0;
  }
}
.formulario h2 {
  padding-top: 3rem;
}

.boton {
  display: block;
  width: 15rem;
  font-family: "Yanone Kaffeesatz", sans-serif;
  color: #FFFFFF;
  text-align: center;
  padding: 1rem 3rem;
  font-size: 1.8rem;
  text-transform: uppercase;
  font-weight: 700;
  margin-bottom: 2rem;
  margin-left: 3rem;
  border: none;
  background: #654ea3; /* fallback for old browsers */
  background: -webkit-linear-gradient(to right, #eaafc8, #654ea3); /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to right, #eaafc8, #654ea3); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}

.campo {
  display: flex;
  margin-bottom: 2rem;
  padding-right: 2rem;
}

.campo__label {
  flex: 0 0 9rem;
  text-align: right;
  padding-right: 2rem;
}

.campo__field {
  flex: 1;
  border: 1px solid #e3e1e1;
}

.campo__field--textarea {
  height: 10rem;
}

footer {
  background-color: #d0697c;
  text-align: center;
  padding: 0.5rem 0 0.5rem 0;
}